.dropbox {
  outline: 2px dashed grey;
  /* the dash box */
  outline-offset: -10px;
  background: lightcyan;
  color: dimgray;
  padding: 10px 10px;
  min-height: 200px;
  /* minimum height */
  cursor: pointer;
  width: 100%; }

.dropbox:hover {
  background: lightblue;
  /* when mouse over to the drop zone, change color */ }

.dropbox p {
  font-size: 1.2em;
  text-align: center;
  padding: 50px 0; }



