.spreadsheet-tabs .nav-tabs {
  /*border-bottom: 1px solid #dee2e6;*/
  /*border-top: 1px solid #dee2e6;*/
  border: 1px solid #dee2e6;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
  padding-bottom: 0.2rem; }

.spreadsheet-tabs .nav-tabs .nav-item {
  margin-top: -1px; }

.spreadsheet-tabs .nav-tabs .nav-link {
  border: 1px solid transparent;
  border-top-left-radius: initial;
  border-top-right-radius: initial;
  border-bottom-left-radius: 0.15rem;
  border-bottom-right-radius: 0.15rem; }

.spreadsheet-tabs .nav-tabs .nav-link:hover, .spreadsheet-tabs .nav-tabs .nav-link:focus {
  border-color: #e9ecef #e9ecef #dee2e6; }

.spreadsheet-tabs .nav-tabs .nav-link.active,
.spreadsheet-tabs .nav-tabs .nav-item.show .nav-link {
  color: #495057;
  background-color: #fff;
  border-color: #fff #dee2e6 #dee2e6; }



